.advantage {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: left;
    margin: 6rem 0;
}

.advantages-container {
    margin: 5rem 10rem;
}

.advantage-div {
    /* width: 90%;  */
    height: 300px;
}

.advantage-div img {
    width: 100%; 
    height: 100%;
    object-fit: contain;
}

.extra-advantage {
    max-width: 480px;
    height: 300px;
    text-align: left;

}

.extra-advantage > h1 {
    font-weight: 700;
    font-size: 40px;
    line-height: 67px;
}

.extra-advantage > p {
    font-weight: 500;
    font-size: 15px;
    /* line-height: 21px; */
    color: rgba(21, 21, 49, 0.8);
}

@media screen and (max-width: 768px) {
    .advantage-div {
        width: 100%; 
        height: auto; 
    }
}
